import React from 'react';
import Modal from '../../common/Modal';
import WuliNumberModal from '../../common/WuliNumberModal';
import MembersUserModal from '../../common/MembersUserModal';
import axios from 'axios';
import md5 from 'blueimp-md5';
import { generateSign, app_key, app_secret, version, host } from '../../config';
import './members.css';

class Pay extends React.Component {
    constructor() {
        super();
        this.state = {
            modalShow: false,
            price: 0,
            hint: '',
            hintText: '',
            wuliModalShow: false,
            uid: '',
            membersUserModalShow: false,
            userinfo: null,
            currentMembersCard: 0,
        }
        this.buyMembershipClick = this.buyMembershipClick.bind(this);
        this.modalClose = this.modalClose.bind(this);
        this.confirm = this.confirm.bind(this);
        this.wuliModalClose = this.wuliModalClose.bind(this)
        this.handleWuliModalShow = this.handleWuliModalShow.bind(this);
        this.handleUidChange = this.handleUidChange.bind(this);
        this.membersUserModalClose = this.membersUserModalClose.bind(this);
        this.membersUserModalConfirm = this.membersUserModalConfirm.bind(this);
    }
    buyMembershipClick() {
        this.setState({
            modalShow: true,
        })
    }
    modalClose() {
        this.setState({
            modalShow: false,
            hint: false,
            hintText: '',
        })
    }
    valid(uid) {
        let isOk = true;
        if(!uid){
            this.setState({
                hint: true,
                hintText: '请输入唔哩号'
            })
            isOk = false;
        }else if(uid.length <8) {
            this.setState({
                hint: true,
                hintText: '唔哩号不能小于8位，请检查后重新输入'
            })
            isOk = false;
        }else if(uid.length >= 8 && uid.length <= 10){
            this.setState({
                hint: false,
                hintText: '',
            })
        }
        else if(uid.length > 10) {
            this.setState({
                hint: true,
                hintText: '唔哩号不能大于10位，请检查后重新输入'
            })
            isOk = false;
        }
        return isOk;
    }
    confirm() {
        const {uid} = this.state;
        const isOk = this.valid(uid);
        if(!isOk) return false;
        const action = "WEB-FRIEND-DETAIL";
        const time = new Date().valueOf();
        const from = 'web';
        const orig_sign = generateSign({
            app_key,
            app_secret,
            time,
            version,
            action,
            user_token: '',
        })
        const sign = md5(orig_sign + time);
        axios({
            method: 'post',
            url: `${host}/service/relation/web-friend-detail.htm`,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            params: {
                action,
                app_key,
                app_secret,
                time,
                version,
                sign,
                friend_uid: uid,
                from,
            }
        })
        .then((response) => {
            const {data} = response;
            if(data.code === 200) {
                this.setState({
                    userinfo: data.data,
                    modalShow: false,
                    membersUserModalShow: true,
                })
            }else{
                this.setState({
                    hint: true,
                    hintText: data.message
                })
            }
        })
        .catch((e) => {
            console.log('error', e);
        })
        
        if(this.state.hint) {
            this.setState({
                hint: false,
                hintText: '',
            })
        }
    }
    wuliModalClose() {
        this.setState({
            wuliModalShow: false
        })
    }
    handleWuliModalShow() {
        this.setState({
            wuliModalShow: true,
        })
    }
    handleUidChange(e) {
        this.setState({
            uid: e.target.value,
        })
    }
    membersUserModalClose() {
        this.setState({
            membersUserModalShow: false,
        }) 
    }
    membersUserModalConfirm() {
        const {uid} = this.state;
        const action = "WEIXIN-WEB-TRADE-CONSTRUCT";
        const time = new Date().valueOf();
        const from = 'web';
        const orig_sign = generateSign({
            app_key,
            app_secret,
            time,
            version,
            action,
            user_token: '',
        })
        const sign = md5(orig_sign + time);
        axios({
            method: 'post',
            url: `${host}/service/pay/weixin/web-trade-construct.htm`,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            params: {
                action,
                app_key,
                app_secret,
                time,
                version,
                sign,
                uid,
                user_token: '',
                recharge_id: '',
                from,
            }
        })
        .then((response) => {
            const {data} = response;
            if(data.code === 200) {
                console.log('data.data',data.data)
            }else{
                this.setState({
                    hint: true,
                    hintText: data.message
                })
            }
        })
        .catch((e) => {
            console.log('error', e);
        })
    }
    handleMembersCardClick = (index) => (e) => {
        this.setState({
            currentMembersCard: index,
        })
    }
    render() {
        return (
            <div>
                <div className="members_wrap">
                    <div className="banner">
                    </div>
                    <div className="members_bg"></div>
                </div>
                <div 
                    className="members_button"
                    onClick={this.buyMembershipClick}
                >购买会员</div>
                <Modal
                    show={this.state.modalShow}
                    close={this.modalClose}
                    confirm={this.confirm}
                    hint={this.state.hint}
                    hintText={this.state.hintText}
                    handleWuliModalShow={this.handleWuliModalShow}
                    onUidChange={this.handleUidChange}
                />
                <WuliNumberModal
                    show={this.state.wuliModalShow}
                    close={this.wuliModalClose}
                />
                <MembersUserModal
                    show={this.state.membersUserModalShow}
                    close={this.membersUserModalClose}
                    userinfo={this.state.userinfo}
                    confirm={this.membersUserModalConfirm}
                    curIndex={this.state.currentMembersCard}
                    handleMembersCardClick={this.handleMembersCardClick}
                />
            </div>
        )
    }
}

export default Pay;


