import React from 'react';
import header_bg from '../../assets/images/members_header.png';
import x from '../../assets/images/x@3x.png';

const Card = ({amount, price, promote, cardClick, curIndex, index}) => (
    <div
        style={{
            width: '2.1rem',
            height: '2.1rem',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            marginLeft: '.05rem',
            marginRight: '.05rem',
            borderRadius: '.1rem',
            background: curIndex === index ? 'linear-gradient(to bottom, #fe992c, #f85834)' : '#fff',
        }}
    >
    <div
        style={{
            width: '2rem',
            height: '2rem',
            borderRadius: '.1rem',
            backgroundColor: '#e6e6e6',
            fontSize: '.28rem',
            letterSpacing: '.02rem',
            color: curIndex === index ? '#fe992c' : '#999',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
        }}
        onClick={cardClick(index)}
    >
        <div>
            <span
                style={{
                    fontSize: '.48rem',
                    color: curIndex === index ? '#fe992c' : '#222',
                }}
            >{amount}</span>
            个月
        </div>
        <div>月均¥{price}</div>
        <div 
            style={{
                fontSize: '.22rem',
                marginTop: '.2rem',
            }}
        >{promote}</div>
    </div>
    </div>
)

const cards = [
    {
        amount: 1,
        price: 12,
        promote: '',
    },
    {
        amount: 6,
        price: 11,
        promote: '立减¥6',
    },
    {
        amount: 12,
        price: 10,
        promote: '立减¥24',
    },
]

class MembersUserModal extends React.Component {
    constructor() {
        super()
        this.handleModalTouchMove = this.handleModalTouchMove.bind(this);
    }

    handleModalTouchMove(e) {
        e.preventDefault()
        e.stopPropagation();
    }

    componentDidMount() {
        document.getElementById('shadow').addEventListener("touchmove", (e) => {
            // 执行滚动回调
            this.handleModalTouchMove(e)
        }, {
            passive: false //  禁止 passive 效果
        })
    }
    render() {
        const {userinfo} = this.props;
        const photo = userinfo && userinfo.photo
        return  (
            <div
                style={{
                    width: '100vw',
                    height: '100vh',
                    position: 'fixed',
                    top: 0,
                    left: 0,
                    zIndex: 2,
                    backgroundColor: 'rgba(0,0,0,.5)',
                    display: this.props.show ? 'flex' : 'none',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'center',
                    overflow: 'hidden',
                    touchAction: 'none',
                }}
                onTouchMove={this.handleModalTouchMove}
                id="shadow"
                onClick={this.props.close}
            >
                <div
                    style={{
                        width: 'calc(100vw - .6rem)',
                        height: '8.36rem',
                        borderRadius: '10px', 
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'flex-start',
                        alignItems: 'center',
                    }}
                    onClick={e=>e.stopPropagation()}
                >
                    <div
                        style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'center',
                            alignItems: 'center',
                            background: `url(${header_bg}) center center / cover no-repeat`,
                            height: '3.5rem',
                            width: 'calc(100vw - .6rem)',
                            borderRadius: '.1rem .1rem 0 0',
                        }}
                    >
                        <div
                            style={{
                                width: '1.4rem',
                                height: '1.4rem',
                                borderRadius: '.7rem',
                                border: '.02rem solid #fff',
                                boxSizing: 'border-box',
                                background: photo ? `url(${photo}) center center / cover no-repeat` : '',
                            }}                    
                        ></div>
                        <div
                            style={{
                                fontSize: '.32rem',
                                color: '#fff',
                                marginTop: '.1rem',
                            }}
                        >
                            {userinfo && userinfo.nickName}
                        </div>
                        <div
                            style={{
                                fontSize: '.24rem',
                                color: 'rgba(255,255,255,.6)',
                                marginTop: '.08rem',
                            }}
                        >
                            唔哩号：{userinfo && userinfo.uid}
                        </div>
                    </div>
                    <div
                        style={{
                            height: '3.86rem',
                            width: 'calc(100vw - .6rem)',
                            backgroundColor: '#fff',
                            paddingTop: '.5rem',
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'flex-start',
                        }}
                    >
                        {
                            cards.map((item, index) => (
                                <Card 
                                    key={index} 
                                    index={index} 
                                    curIndex={this.props.curIndex}
                                    cardClick={this.props.handleMembersCardClick}
                                    {...item} 
                                />
                            ))
                        }
                    </div>
                    <div
                        style={{
                            width: 'calc(100vw - .6rem)',
                            height: '1rem',
                            borderTop: '.01rem solid #e6e6e6',
                            fontSize: '.36rem',
                            color: '#fe992c',
                            borderRadius: '0 0 .1rem .1rem',
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                            backgroundColor: '#fff',
                        }}
                        onClick={this.props.confirm}
                    >
                        在线购买会员
                    </div>
                </div>
                <div
                    style={{
                        width: '.6rem',
                        height: '.6rem',
                        background: `url(${x}) center center / cover no-repeat`,
                        marginTop: '.31rem',
                    }}
                    onClick={this.props.close}
                >
                </div>
            </div>
        )
    }
}


export default MembersUserModal


